import Slide from "react-reveal/Slide";
import Swing from "react-reveal/Swing";
import Fade from "react-reveal/Fade";

export const Info = ({ shaddow, navToMovies }) => {
  return (
    <section className="info">
      <div
        className="text"
        style={{ boxShadow: shaddow ? "-5px 0px 7px $g4-opc" : "" }}
      >
        <Slide right duration={2000}>
          <div className="text-block">
            <p>
              הנתונים קובעים: "DOSTORIES" הוא האימפקט סטורי טלינג החברתי־רגשי
              בקרב נוער, חברות וארגונים בישראל, בתחום האימפקט רגשי חברתי
            </p>
            <span>הגעתם למקום הנכון!</span>
            <p>
              ארגז הכלים המקיף בישראל בתחומי האקלים החברתי, ה־SEL, בליווי סרטים
              חברתיים בעלי השפעה משמעותית
            </p>
          </div>
        </Slide>
        <Slide bottom duration={2000}>
          <div className="btns">
            <Swing delay={2000}>
              <button onClick={navToMovies} className="btn-signup">
                להצטרפות
              </button>
            </Swing>
            <button onClick={navToMovies} className="btn-login">
              כניסה למנויים
            </button>
          </div>
        </Slide>
      </div>
      <div className="img-container">
        <img src={require("../../assets/imgs/demo.jpg")} alt="svg" />
      </div>
    </section>
  );
};
