export const MovieMonth = ({ navToMovie }) => {
  return (
    <div className="movie-month">
      <h2>סרט החודש</h2>
      <h3>לחיות מחדש</h3>

      <div className="movie-container">
        <div className="img-container">
          <img
            src={require("../../assets/imgs/materials/Still 1.jpg")}
            alt="movie-preview"
          />
        </div>
        <div className="movie-month-preview">
          <p className="movie-des">
            "לא מרתיע אותי לפתוח מקום מול הפאב שכמעט נגמרו לי החיים. להיפך - זה
            דווקא מרגש אותי. מבחינתי זו פתיחה של מעגל ולא סגירה שלו". הסרט מגולל
            את סיפורו של עידו לזן שנפצע קשה בתל אביב והחליט לפתוח סטודיו מול
            המקום שבו כמעט נרצח, כדי לעזור לאנשים לחיות טוב יותר באמצעות הכושר.
          </p>
          <button className="btn-watch" onClick={navToMovie}>
            לצפייה בסרט
          </button>
        </div>
      </div>
    </div>
  );
};
